body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.alerts ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.alerts li {
  background-color: #aaa;
  margin-bottom: 5px;
}

.alerts img {
  margin: auto;
}

.alerts p {
  text-align: center;
}

@charset "utf-8";

html {
  height: 100%;
}

body, #root {
  height: 100%;
}

.alert-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 100%;
  margin: 0;
}


.alert-page::before {
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  content: "";
  background: -webkit-linear-gradient(bottom, black, deepskyblue, black);
  background: linear-gradient(0deg, black, deepskyblue, black);
  background-size: contain;
  z-index: -1;
}

p, h2, h3 {
  color: #fff;
}

/* 共通 */
.container {
  margin: 0 auto 0 auto;
}

section {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}

.alert-page > section {
  height: auto;
}

section .container {
  max-width: 1000px;
}

/* inners */
.header-inner {
  padding: 10px 10px 10px 10px;
  background-color: #111;
}

.nav-inner {
  /* background-color: #565656;*/
  background-color: rgba(50, 50, 50, 0.95);
}

.main-inner {
  padding: 20px 10px 20px 10px;
}

.footer-inner {
  background-color: gray;
}

/* header */
/*
.hamburger {
display: block;
margin: 0 0 0 auto;
border: none;
width: 50px;
height: 50px;
background: url(../img/hamburger.png) no-repeat;
background-size: contain;
}
 */

header {
  background-color: black;
}

header h1 {
  background: -webkit-radial-gradient(#ccc, #000);
  background: radial-gradient(#ccc, #000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
  text-align: center;
  font-size: 3em;
}

/* nav */

.navbar {
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 0;
  padding: 0;
}

.navbar li {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  text-align: center;
}

.navbar li a {
  display: block;
  width: 100%;
  padding: 10px 0 10px 0;
  text-decoration: none;
  color: #eee;
}

.navbar li:hover {
  background-color: rgba(135, 135, 135, 0.95);
  margin: -1px;
  border: solid 1px #fff;
}

/* main */

main {
  padding: 0 10px 0 10px;
}

.alert-boxes {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.alert-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  max-width: 470px;
  width: 100%;
  margin: 5px;
  border-top: solid #111;
  border-bottom: solid #111;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.5);
}

.alert-detail, .alert-date {
  padding: 10px;
}

.alert-date {
  border-bottom: solid 1px #aaa;
}
.alert-date p {
  margin: 0 0 5px 0;
  text-align: center;
}

.alert-detail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}

.alert-detail figure {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100px;
  height: 100px;
  margin: auto 0 auto 0;
}

.alert-detail img {
  max-width: 100%;
}

.alert-text-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  padding: 0 10px 0 10px;
  max-width: 500px;
}

.alert-text-box ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.alert-text-box li {
  margin: 0;
}

.alert-text-box p {
  margin: 0;
}

.alert-rest-time {
  border-top: solid 1px #aaa;
  text-align: center;
}

.alert-rest-time p {
  margin: 0;
  font-weight: bold;
  font-size: 1.2em;
}

/* alert text box elemets */

.item {
  font-size: 1.4em;
  color: #0f0;
}

.credits {
  font-size: 1.2em;
  color: deepskyblue;
}

.nightmare {
  color: red;
}

/* footer */
.footer-inner p {
  margin: 0;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .alert-detail figure {
    width: 80px;
    height: 80px;
  }
  
  .alert-boxes {
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-align-items: center;
            align-items: center;
  }
  
  header h1 {
    font-size: 2em;
  }
}


/* test */

.is_fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.is_invisible {
  visibility: hidden;
}

